body {
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.breed-select {
  width: 300px;
  border: 1px solid #fff;
  border-radius: 5px;
  margin-bottom: 20px;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #de1212;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 12px;
  height: 12px;
  border: 2px solid #fff;
  border-radius: 50%;
  position: absolute;
  top: 4px;
  left: 4px;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  text-align: center;
  color: red;
  margin-top: 20px;
  font-weight: bold;
  display: none;
}

.cat-container {
  align-items: flex-start;
  margin-top: 20px;
  display: flex;
}

.cat-image {
  max-width: 600px;
  height: auto;
  margin-right: 20px;
}

.cat-details {
  flex-direction: column;
  flex-grow: 1;
  justify-content: flex-start;
  display: flex;
}

.cat-title {
  margin: 0 0 5px;
  font-size: 20px;
}

.cat-temperament {
  margin-top: 10px;
  font-weight: bold;
}

.cat-description, .cat-temperament {
  max-width: 600px;
  margin: 0;
  font-size: 16px;
  line-height: 1.5;
}

